body{
    background-color: #F6FCFF;
    padding: o;
    margin: 0;
    font-family: "Montserrat",sans-serif;
}

input:focus,textarea:focus{
    outline: none;
}

a,nav button{
    text-decoration: none;
    background-color: transparent;
    border: none ;
    color: #000;
    transition: .4s;
    font-size: 1em;
    cursor: pointer;
    font-family: "Montserrat",sans-serif;
}
a:hover,nav button:hover{
    color: #bbb ;
}
/* estilos dos formulários */
form {
    max-width: 40%;
    margin: 0 auto;
}
label{
    display: flex;
    flex-direction: column;
    margin-bottom: 1em;
}
label span{
    margin-bottom: .3em;
    font-weight: bold;
    text-align: left;
}
input,textarea{
    box-sizing: border-box;
    border: none;
    border-bottom: 1px solid #ccc;
    padding: .8em 0;
    background-color: transparent;
}
/* estilo para entrada de dados */
input::placeholder,textarea::placeholder{
    font-family: "Montserrat",sans-serif;  
    color: #aaa;
}

.botao{
    color: #fff;
    text-align: center;
    cursor: pointer;
    border-radius:10px ;
    width: 120px;
    font-weight: bold;
    border: none;
    background-color: #1a8918;
    padding: 10px 15px;
}
.botao.botao-dark{
    background-color:#000 ;
    border-radius: 0;
}
.botao.botao-outline{
    background-color: transparent ;
    border-radius: 0;
    color: #000;
    border: 1px solid #000;
    padding: 7px 30px;
}
.botao.botao-outline{
    background-color: #000;
    color: #aaa;
}
.botao.botao-danger:hover{
    background-color: #dc3545;
}
button[disabled]{
    background-color: #aaa;
}
.container .error{
    color: #721c24;
    background-color: #f8d7da;
    border: 1px solid #f5c6cb ;
    padding:5px ;
    border-radius: 5px;
}